import React, { useEffect, useState } from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Checkbox from '@mui/material/Checkbox';
import InputLabel from '@mui/material/InputLabel';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import Link from '@components/link';

const Contact = () => {
  const clientnameId = 'contact__formClientname';
  const emailId = 'contact__formEmail';
  const email2Id = 'contact__formEmail2';
  const addressId = 'contact__formAddress';
  const inquiryId = 'contact__formInquiry';

  const [forms, setForms] = useState({
    [clientnameId]: '',
    [emailId]: '',
    [email2Id]: '',
    [addressId]: '',
    [inquiryId]: ''
  });

  const [isInvalids, setIsInvalids] = useState({
    [emailId]: false,
    [email2Id]: false
  });

  const checkForm = () => {
    let isInvalidEmailId = false;
    let isInvalidEmail2Id = false;

    if (
      forms[emailId] &&
      forms[email2Id] &&
      forms[emailId] !== forms[email2Id]
    ) {
      isInvalidEmailId = true;
      isInvalidEmail2Id = true;
    }

    setIsInvalids({
      ...isInvalids,
      [emailId]: isInvalidEmailId,
      [email2Id]: isInvalidEmail2Id
    });
  };

  useEffect(() => {
    checkForm();
  }, [forms]);

  const onChangeForm = (key, value) => {
    setForms({
      ...forms,
      [key]: value
    });
  };

  return (
    <form
      action='https://docs.google.com/forms/u/2/d/e/1FAIpQLSezATzl5zb4rVgOMdghXC1LhyhzXYIVaytaFpF0Qh4d_fi1_Q/formResponse'
      method='POST'
    >
      {[
        {
          name: 'entry.1650788426',
          id: clientnameId,
          required: true,
          label: 'お名前',
          placeholder: '達磨 大志'
        },
        {
          id: emailId,
          type: 'email',
          required: true,
          label: 'メールアドレス',
          placeholder: 'taishi@daruma-inv.com'
        },
        {
          name: 'entry.1696363940',
          id: email2Id,
          type: 'email',
          required: true,
          label: 'メールアドレス（確認用）',
          placeholder: 'taishi@daruma-inv.com',
          invalidMessage: 'メールアドレスが一致していません。',
          invalidPaste: true
        },
        {
          name: 'entry.1411341641',
          id: addressId,
          label: 'ご住所',
          placeholder: '東京都渋谷区千駄ヶ谷2-10-5'
        },
        {
          name: 'entry.881256958',
          id: inquiryId,
          required: true,
          label: 'お問い合わせ内容',
          placeholder:
            '不動産の購入・売却、などなどお気軽にお問い合わせください。',
          multiline: true,
          minRows: 5
        }
      ].map(
        (
          {
            name = '',
            id,
            type = 'text',
            required,
            label,
            placeholder,
            multiline,
            minRows,
            invalidMessage,
            invalidPaste
          },
          i
        ) => (
          <Box key={i} mb={8}>
            <InputLabel
              htmlFor={id}
              sx={{
                color: 'text.primary',
                mb: 2
              }}
            >
              <Typography
                component='span'
                fontSize={10}
                fontWeight='bold'
                px={0.5}
                mr={1}
                color={required ? 'common.white' : '#333'}
                bgcolor={required ? 'text.primary' : 'text.secondary'}
              >
                {required ? '必須' : '任意'}
              </Typography>
              {label}
            </InputLabel>
            <TextField
              variant='standard'
              name={name}
              id={id}
              fullWidth
              required={required}
              type={type}
              multiline={multiline}
              minRows={minRows}
              placeholder={placeholder}
              value={forms[id]}
              error={isInvalids[id]}
              helperText={isInvalids[id] && invalidMessage}
              onChange={e => onChangeForm(id, e.target.value)}
              onPaste={e => {
                if (invalidPaste) {
                  e.preventDefault();
                  return false;
                }
              }}
            />
          </Box>
        )
      )}
      <Typography my={3} color='text.primary'>
        <Checkbox required />
        <Link href='/privacy/' target='_blank'>
          プライバシーポリシー
        </Link>{' '}
        に同意します。
      </Typography>
      <Button
        type='submit'
        fullWidth
        sx={{
          bgcolor: 'text.secondary',
          height: 80
        }}
      >
        <Typography color='#151618'>上記の内容で送信する</Typography>
      </Button>
      <Typography mt={2} color='#707070' variant='body2'>
        ※上のフォームから送信がうまくいかない場合は{' '}
        <Link
          href='https://docs.google.com/forms/d/e/1FAIpQLSezATzl5zb4rVgOMdghXC1LhyhzXYIVaytaFpF0Qh4d_fi1_Q/viewform'
          target='_blank'
        >
          こちら
          <i className='fas fa-external-link-alt'></i>
        </Link>{' '}
        からお問い合わせください。
      </Typography>
    </form>
  );
};

export default Contact;
